<template>
  <div id="myCarousel" class="carousel slide d-flex flex-column justify-content-center align-items-center"
       data-bs-ride="carousel">
    <div class="carousel-indicators">
      <button type="button" data-bs-target="#myCarousel" :data-bs-slide-to="index" class="active" aria-current="true"
              :aria-label="'Slide'+index " v-for="(item, index) in sliders" v-bind:key="index"></button>
    </div>
    <div class="carousel-inner">
      <div class="carousel-item active " v-for="(item, index) in sliders" v-bind:key="index">
        <img v-bind:src="this.imageUrl(item.slider_img)" class="bd-placeholder-img">
        <div class="container">
          <div class="carousel-caption text-start">
            <h1>{{item.title}}</h1>
            <p>{{item.description}}</p>
          </div>
        </div>
      </div>
    </div>
    <button class="carousel-control-prev" type="button" data-bs-target="#myCarousel" data-bs-slide="prev">
      <span class="carousel-control-prev-icon" aria-hidden="true"></span>
      <span class="visually-hidden">Previous</span>
    </button>
    <button class="carousel-control-next" type="button" data-bs-target="#myCarousel" data-bs-slide="next">
      <span class="carousel-control-next-icon" aria-hidden="true"></span>
      <span class="visually-hidden">Next</span>
    </button>
  </div>
</template>

<style scoped>

</style>

<script>
import axios from "axios";

export default {
  mounted() {
    console.log('Component mounted.')
    console.log(process.env.VUE_APP_API_URL)
    this.getSliders();
  },
  data() {
    return {
      sliders: []
    };
  },
  computed: {},
  methods: {
    async getSliders() {
      axios
          .get(process.env.VUE_APP_API_URL + 'sliders')
          .then(response => {
            this.sliders = response.data.data;
            console.log("DATA")
            console.log(this.sliders)
            console.log("DATA")
          })
          .catch(error => {
            console.log(error)
            this.errored = true
          })
          .finally(() => this.loading = false)
    },
    imageUrl(image) {
      return image;
    }
  }
}
</script>