import {createWebHistory, createRouter} from "vue-router";
import Home from "@/pages/HomePage.vue";
import AboutUs from "@/pages/AboutUsPage.vue";
import ServicePage from "@/pages/ServicePage.vue";
import ContactUs from "@/pages/ContactUsPage.vue";
import Faq from "@/pages/FaqPage.vue";
import TermsCondition from "@/pages/TermsConditionPage.vue";
import PrivacyPolicy from "@/pages/PrivacyPolicyPage.vue";
import Testimonial from "@/pages/TestimonialPage.vue";

const routes = [
    {
        path: "/",
        name: "Home",
        component: Home,
    },
    {
        path: "/about",
        name: "About",
        component: AboutUs,
    },
    {
        path: "/about",
        name: "Service",
        component: ServicePage,
    },
    {
        path: "/contact-us",
        name: "ContactUs",
        component: ContactUs,
    }, {
        path: "/faq",
        name: "Faq",
        component: Faq,
    }, {
        path: "/terms",
        name: "TermsCondition",
        component: TermsCondition,
    }, {
        path: "/privacy-policy",
        name: "PrivacyPolicy",
        component: PrivacyPolicy,
    },{
        path: "/testimonials",
        name: "Testimonial",
        component: Testimonial,
    },
];

const router = createRouter({
    history: createWebHistory(),
    routes,
});

export default router